.app-container {
  text-align: center;
  font-family: Arial, sans-serif;
}

.app-header {
  margin-bottom: 20px;
}

.app-title {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.countdown-timer {
  font-size: 2em;
  margin-bottom: 20px;
  color: #f0a500; /* Gold color */
}

.answer-questions-button {
  background-color: #b58d2b; /* Gold background color */
  color: black; /* Black text color */
  padding: 20px 40px;
  border: none;
  border-radius: 25px;
  font-size: 1.5em;
  cursor: pointer;
  margin: 20px auto;
  display: block; /* Center the button */
  width: 80%; /* Adjust the width to be responsive */
}

.answer-questions-button:hover {
  background-color: #d4af37; /* Slightly lighter gold on hover */
}

.leaderboard-container {
  width: 80%; /* Adjust the width to be responsive */
  margin: 0 auto; /* Center the container */
  text-align: left;
}

.leaderboard {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.leaderboard th,
.leaderboard td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: center;
  color: white;
}

.leaderboard th {
  background-color: #b58d2b; /* Gold background color */
  color: black; /* Black text color */
}

.leaderboard tr:nth-child(even) {
  background-color: #333; /* Dark background for even rows */
}

.leaderboard tr:nth-child(odd) {
  background-color: #444; /* Slightly lighter background for odd rows */
}

.leaderboard tr.highlight-reset {
  background-color: red; /* Red background for the last row */
  color: white; /* White text for the last row */
}

.leaderboard td {
  color: white;
  font-size: 1em;
}

.task-form {
  margin-bottom: 20px;
  color: white; /* Set text color to white */
}

.task-form table {
  width: 100%;
  table-layout: auto;
  margin-bottom: 20px;
  border-collapse: collapse;
}

.task-form th, .task-form td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.task-form label, 
.task-form input {
  color: white; /* Ensure inputs also have white text */
}
